import React from "react"
import Anime from 'react-anime'
import { useStaticQuery, Link, graphql } from "gatsby"
import 'bootstrap/dist/css/bootstrap.css';
import { Container, Row, Col } from "react-bootstrap"
import "../components/app.css"
import Layout from "../components/layout.js"
import Img from "gatsby-image"


const AboutPage = () => {
	const data = useStaticQuery(graphql`
    query {
      imageOne: file(relativePath: { eq: "me.JPG" }) {
	      childImageSharp {
			    fluid(maxWidth: 1000) {
			      ...GatsbyImageSharpFluid
			    }
			  }
	    }
	  }
  `)
  return (
	  <Layout>
	  	<Container fluid={true}>
	  		<Anime opacity={[0, 1]} translateY={'1em'} delay={(e, i) => 200}>
	  			<section>
		  		<Row>
		  			<Col xs={12} sm={12} md={8} lg={6} xl={6}>
		  				<Img fluid={data.imageOne.childImageSharp.fluid} />
		  				<br /><br />
		  			</Col>
		  			<Col xs={12} sm={12} md={8} lg={6} xl={6}>
		  				<h1 className="project-h">Hi I'm Christie!</h1>
					    <p>​​​​​​I graduated from Carnegie Mellon University (CMU) with a B.S. in <a href="https://www.cmu.edu/information-systems/" target="_blank" className="link"> Information Systems (IS) </a> and minors in <a href="https://hcii.cmu.edu" className="link"> Human-Computer Interaction (HCI) </a> and <a href="https://www.meche.engineering.cmu.edu/" target="_blank" className="link">Engineering</a>.</p>
					    <p>For the first two years at CMU, I studied Mechanical Engineering because I was always fascinanted by how things worked and how they were made. During my freshman year, I took my first Computer Science course, which sparked my interest in <i>how we interact with computer programs, apps and technology</i>. Afterwards, I pursued a minor in HCI and ultimately switched to Information Systems. I then delved myself into the UX field, gaining insight and experience from listening to UX podcasts (I'd recommend <a href="https://userdefenders.com/" className="link">User Defenders</a>) and applying UX to several side projects. Through my IS curriculum, I was able to gain skills in both frontend and backend development as well.</p>
					 
					    <p>During college, I was involved in extracurriculars such as Greek Life, A Cappella, and First-year Orientation. Through these involvements on campus, I have learned invaluable skills in leadership, conflict management, team management, event planning, and other skills I would not have learned in a classroom. In my free time, I love being around nature, sketching, exploring foods, cooking and practicing the piano.</p>

					    <p>I interned at Apple in Summer 2018 and will be returning as a UX designer in Fall 2019.</p>
					    
					    <p><a href="mailto:cchang2@alumni.cmu.edu" className="link">Let's talk!</a></p>
		  			</Col>
		  		</Row>
		  		</section>
	  		</Anime>
			</Container>
	  </Layout>
  )
}

export default AboutPage
